import React, {useEffect, useState} from 'react';

import Header from "./Header";
import MovieContainerR from "./MovieContainerR";
import {BrowserRouter, Route, Switch} from "react-router-dom";
import Serials from "./Serials";
import TVContainerR from "./TVContainerR";
import Test from "./Test";

import "./Main.css"
import WithRouterMovieInfo from "./MovieInfoContainer";


function App() {


    return (<div className={"innerBody"}>

       <BrowserRouter>
           <Header/>
           <Switch>
               <Route path={"/tv"}> <TVContainerR/> </Route>

               <Route exact path={"/movies"}> <MovieContainerR/> </Route>
               <Route  path={"/movies/:movieId"}> <WithRouterMovieInfo/> </Route>

           </Switch>

       </BrowserRouter>

    </div>);
}

export default App;
