import * as React from 'react';
import hd from "./Header.module.css"
import logo from "./images/logo.png"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faAngleDown,faSearch,faMoon,faUserTie} from '@fortawesome/free-solid-svg-icons'
import navbars from "./Navbar.module.css"
import { Link } from "react-router-dom"
import {NavLink} from "react-router-dom";
const Header=(props:any)=>{

    return(<div className={hd.HeaderBody}>

        <div className={hd.navBar}>
            <img src={logo} className={hd.logo}></img>
            <div>
            <ul className={hd.navBarMain}>
                <Link style={{ textDecoration: 'none' ,cursor:"inherit"}}  to={"/tv"} >   <li> <span  className={hd.alone}>ТБ</span></li></Link>
                <Link style={{ textDecoration: 'none' ,cursor:"inherit"}} to={"/movies"} >   <li> <span  className={hd.alone} >Фільми</span></li></Link>
                <Link style={{ textDecoration: 'none' ,cursor:"inherit"}} to={"/premiers"} > <li> <span className={hd.alone}>Прем'єри</span></li></Link>
                <Link style={{ textDecoration: 'none' ,cursor:"inherit"}} to={"/tarif"} > <li> <span className={hd.alone}>Тарифи</span></li></Link>
                <li> <Link style={{ textDecoration: 'none' ,cursor:"inherit"}} to={"/how"} >  <span className={hd.alone}> Як підключити</span></Link></li>
                <Link style={{ textDecoration: 'none' ,cursor:"inherit"}} to={"/tv"} >     <li> <span className={hd.alone}>Промокод</span></li></Link>
                <li  className={hd.moreList}> <span className={hd.alone}>Ще <FontAwesomeIcon className={hd.iconFa} icon={faAngleDown} />
                    <ul  className={hd.more}>
                        <ul  className={hd.test}>
                            <Link style={{ textDecoration: 'none' ,cursor:"inherit"}}  to={"/tv"} >    <li><span className={hd.alone}>Оплата</span></li></Link>
                            <Link style={{ textDecoration: 'none' ,cursor:"inherit"}} to={"/tv"} >   <li><span className={hd.alone}>Серіали</span></li></Link>
                            <Link style={{ textDecoration: 'none' ,cursor:"inherit"}} to={"/tv"} >  <li><span className={hd.alone}>Мультфільми</span></li></Link>


                        </ul>


                </ul>
                </span>
               </li>
            </ul>
            </div>
        <div className={hd.navBarOther}>
            <a><FontAwesomeIcon icon={faSearch} size={"lg"} /></a>
            <a><FontAwesomeIcon icon={faMoon} size={"lg"} /></a>
            <a>UK<FontAwesomeIcon icon={faAngleDown} size={"lg"} /></a>
            <a><FontAwesomeIcon icon={faUserTie} size={"lg"} />Увійти</a>
        </div>
        </div>


</div>)

}

export default Header;