
import React, {useEffect, useState} from 'react';
import {connect} from "react-redux";
import './App.css';
import {api} from "./api";
import {setGenresAC, setTVAC} from "./TVReducer";
import TV from "./TV";

const TVContainer=(props)=>{

    useEffect(() => {

        api.getTV().then((data) => {
            return data.results
        }).then((result) => {
            props.setTVAC(result);
        });
        api.getGenre().then((data)=>{

            props.setGenresAC(data.genres);

        })


    }, [])

    return (<div className={"innerBody"}>
        <div className="App">

            {props.describe.length > 1 ? props.describe.map((u) => <TV genres={props.genres} key={u.id} tv={u}  />) : <div>PRELOADER</div>}
    </div>
    </div>);
}



let stateGo=(state)=>{

    return {

    describe:state.tv.details,
        genres:state.tv.genres,
    }
};

const TVContainerR=connect(stateGo,{
    setTVAC,
    setGenresAC,
})(TVContainer);
export default TVContainerR;