import * as React from 'react';
import "./Movie.css"
import * as hd from "./MovieInfo.module.css"
const API_IMAGES="https://image.tmdb.org/t/p/w1280";
const MovieInfo=(props)=>{
    let test=props.movie.original_title;




    let getShortGenre=(genres2)=>{
        let strGenre="";
        for (let i=0;i<genres2.length;i++){
            strGenre=strGenre+genres2[i];
        }
        if (strGenre.length<25){
            return   strGenre;} else return strGenre.slice(0,100)+"..."}
            let shortName=getShortGenre(props.credits)



return(<div className={hd.main} >
    {test ?
        <div className={hd.describe}>
        <div className={hd.info}>
            <div className={hd.firstColumn}>

                <li> Рік</li>
                <li> Рейтинг</li>
                <li> Країна</li>
                <li> Жанр</li>
                <li> Режисер</li>
                <li> У ролях</li>
                <li> Вік</li>
                <li> Тривалість</li>
            </div>
            <div className={hd.secondColumn}>
                <li> {props.movie.release_date}</li>
                <li> {props.movie.vote_average}</li>
                <li> -------</li>
                <li> -------</li>
                <li> -------</li>

                <li> {shortName}</li>
                <li> -------</li>
            </div>
            <div>
                <img className={hd.image} src={API_IMAGES + props.movie.backdrop_path}></img>
            </div>

        </div>
        <div>

        </div>
       </div>
        :null

    }
</div>)
}

export default MovieInfo