import * as React from 'react';
import { withRouter } from "react-router";
import {useEffect} from "react";
import {connect} from "react-redux";
import {setGenresAC, setMoviesAC} from "./FilmsReducer";
import {compose} from "redux";
import {api} from "./api";
import {useState} from "react";
import MovieInfo from "./MovieInfo";




const MovieInfoContainer=(props)=> {

    const [movie,setMovie]=useState({})
    const [credits,setCredits]=useState({})
    const [arrMainRoles,setArrMainRoles]=useState("")

    useEffect(() => {

        api.getFilm(props.match.params.movieId).then((data) => {

            setMovie(data);
        });
        api.getCredits(props.match.params.movieId).then((data)=>{

            setCredits(data.cast);
            data.cast.map((obj)=>{
                debugger
                if(obj.hasOwnProperty("character")) setArrMainRoles((prevState)=>{setArrMainRoles(prevState+obj.name+`,`)})

            })
        })



    }, [])

    return(<div>
<MovieInfo credits={arrMainRoles} movie={movie} roles={arrMainRoles}/>


</div>)
};
let stateGo=(state)=>{
    return {
        describe:state.films.details,
        genres:state.films.genres,
    }
};

export default compose(
    withRouter,
    connect(stateGo,{ setMoviesAC,setGenresAC,}))(MovieInfoContainer);